import axios from 'axios'
import router from '../router'

// axios.defaults.baseURL = process.env.VUE_APP_BASE_URL
axios.defaults.timeout = 5000

// axios.interceptors.request.use(config => {
//   config.headers.Authorization = sessionStorage.getItem('token')
//   return config
// }, error => {
//   return Promise.error(error)
// })

axios.interceptors.response.use(res => {
  return Promise.resolve(res)
}, error => {
  if (error && error.response) {
    switch (error.response.status) {
      case 400:
        error.message = '';
        break
      case 401: {
        // tokenLogin
        error.message = 'token'
        router.replace({
          path: '/login'
        })
        break
      }
      case 403:
        error.message = ',';
        break
      case 404:
        error.message = ``;
        break
      case 408:
        error.message = '';
        break
      case 500:
        error.message = '';
        break
      case 501:
        error.message = '';
        break
      case 502:
        error.message = '';
        break
      case 503:
        error.message = '';
        break
      case 504:
        error.message = '';
        break
      case 505:
        error.message = 'HTTP';
        break
      default:
        break
    }
  }
  return Promise.reject(error)
})
export function get(url, params) {
  return new Promise(function (resolve, reject) {
    axios.get(url, {
      params: params
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

export function post(url, data) {
  return new Promise(function (resolve, reject) {
    axios.post(url, data)
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

export function del(url, data) {
  return new Promise(function (resolve, reject) {
    axios.delete(url, {
      data: data
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}

export function put(url, data) {
  return new Promise(function (resolve, reject) {
    axios.put(url, {
      data: data
    })
      .then(function (res) {
        resolve(res)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}